import React from 'react';

const footer = () => {
	return (
		<footer className="footer">
			<div className="content has-text-centered">
				<p>
					<strong>Currify</strong> by <a href="https://github.com/sandpatrol1">Sandpatrol</a>.
				</p>
			</div>
		</footer>
	);
};

export default React.memo(footer);



